    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding: 0 3px;
}

.menu__link:hover {
    color: #fbe1db;
}

.menu__link--active {
    color: #fbe1db;
    border-top: 4px solid #fbe1db;
    font-weight: 700;
}

.menu__logo {
    background-image: url(/react-gh-pages/static/media/logo2.6870f163.png);
    text-indent: -9999999px;
    display: inline-block;
    height: 200px;
    width: 200px;

    background-repeat: no-repeat;
    background-position: center;
    margin: 0;
}

.menu__search-button {
    height: 42px;
    width: 42px;
    border-radius: 50%;
    border: 1px solid #c8c9ce;
    background-color: transparent;
    outline: none;
    cursor: pointer;
    background-repeat: no-repeat;
    background-position: center;
}

.menu__search-form {
    position: absolute;
    top: 100%;
